@import './global.less';

html {
	color: #234656;
}

a {
	text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
	line-height: 1.2;
}
h2 {
	font-size: 1.4rem;
	margin: 10px 0;
}

h3 {
	font-size: 1rem;
}

h4 {
	font-size: .9rem;
}

.logo {
	width: 140px;
}
