// 		text-decoration: underline;
// 	}
// }

.desc {
	font-size: .7rem;
	line-height: 1.2;
}

.nav {
	background-color: #234656;
	display: inline-block;
	color: #fff;
	text-decoration: none;
	padding: 8px 16px;
	font-size: .65rem;
	margin-top: 1rem;
	line-height: 1;
	border-radius: 22px;
}

.tabbar {
	position: relative;
	z-index: 1;
	padding: .6rem;
	display: flex;
	box-shadow: #dedede 0 4px 16px 5px;
	justify-content: space-between;
	overflow: visible;
	align-items: center;
	.logo {
		align-items: center;
	}
	.language {
		position: relative;
		overflow: visible;
		.select {
			position: absolute;
			bottom: -56px;
			background-color: #fff;
			border-radius: 6px;
			left: 50%;
			margin-left: -50px;
			border: 1px solid #dedede;
			text-align: center;
			box-shadow: #dedede 0 0 30px 0px;
			display: none;
			a {
				text-decoration: none;
				display: inline-block;
				color: #999;
				width: 100%;
				&:last-child {
					border-bottom: none;
				}
				font-size: .65rem;
				&:hover {
					color: #234656;
					text-decoration: underline;
				}
				&.current {
					cursor: default;
					color: #234656;
					text-decoration: none;
				}
			}
		}
		&:hover {
			.select {
				display: block;
			}
		}
		.text {
			position: relative;
			cursor: pointer;
			margin-right: 10px;
			padding: 0 12px;
			font-size: .85rem;
			&::after {
				content: '';
				position: absolute;
				right: 0;
				top: 50%;
				margin-top: -3px;
				margin-right: 0;
				border-left: 4px solid transparent;
		    border-top: 6px solid #777;
		    border-right: 4px solid transparent;
			}
		}
	}
}

.header {
	background-image: url(../../static/images/header_bg.png);
	background-repeat: no-repeat;
	width: 100%;
	background-size: 600px 600px;
	// background-attachment: fixed;
	background-position: right;
	position: relative;
	height: 60vh;
	display: flex;
	.left {
		.img {
			display: none;
		}
		flex-basis: 48%;
		position: relative;
		z-index: 1;
		padding-top: 10px;
		padding-left: 1.6rem;
		// width: 50%;

		.title {
			width: 0;
			height: 0;
			overflow: hidden;
		}

		h2 {
			margin-top: 2rem;
			font-size: 1.7rem;
		}
		
		h4 {
			color: #54bcd6;
			margin-bottom: 10px;
		}
		.desc {
			font-size: .6rem;
			color: #888;
		}
	}
}
.page {
	padding: 40px 20px;
	display: flex;
	h3 {
		margin-top: 1rem;
		margin-bottom: 10px;
		font-size: 1.5rem;
	}
	.left {
		padding-left: 4rem;
	}
	.right {
		padding-right: 4rem;
	}
	.desc {
		color: #888;
	}
	img {
		margin-top: 2rem;
	}
}

.page-1 {
	background-color: #f8f9fa;
	.left {
		flex-basis: 55%;
		padding-left: 4rem;
		h3 {
			margin-bottom: 5px;
		}
		.desc {
			color: #888;
		}
		.level {
			position: relative;
			margin-top: 10px;
			max-width: 520px;
			p {
				display: inline-block;
				width: 80%;
				text-align: center;
				font-size: .65rem;
				color: #fff;
				border-radius: 20px;
				padding: 10px 12px;
				&:nth-of-type(2n+1) {
					background-color: #234656;
				}
				&:nth-of-type(2n) {
					background-color: #2d5f58;
				}
			}
			.triangle {
				display: block;
				width: 6px;
				position: relative;
				left: 40%;
				margin-left: -5px;
				text-align: center;
				border-left: 6px solid transparent;
		    border-top: 10px solid #234656;
		    border-right: 6px solid transparent;
		    &:nth-of-type(2n) {
		    	border-top: 10px solid #2d5f58;
		    }
		    margin-bottom: 5px;
			}
		}
	}
	.right {
		flex-basis: 45%;
		img {
			width: 70%;
			max-width: 320px;
		}
		.desc {
			color: #333;
			margin-top: 1rem;
		}
	}
}

.page-2 {
	.left {
		flex-basis: 50%;
		text-align: center;
		img {
			width: 50%;
			max-width:280px;
		}
	}
	.right {
		flex-basis: 45%;
		padding-left: 4rem;
		padding-right: 3.5rem;
	}
}

.extra {
	background-color: #f8f9fa;
	padding: 3rem 1rem;
	text-align: center;
	h3 {
		text-align: center;
		font-size: 1.5rem;
	}
	h4 {
		font-size: 1.1rem;
	}
	.wrap {
		display: flex;
		justify-content: space-around;
		.box {
			flex-basis: 30%;
			padding: 20px;
			max-width: 340px;
			text-align: center;
			img {
				width: 3.2rem;
			}
			.desc {
				color: #333;
			}
		}
	}
}

.footer {
	background-color: #55bdd6;
	background-image: linear-gradient(to right, #55bdd6, #d2df98);
	color: #fff;
	font-size: .65rem;
	.wrap {
		display: flex;
		.left {
			flex-basis: 60%;
		}
		.right {
			flex-basis: 40%;
			text-align: right;
		}
	}
	.copyright {
		margin: 0 auto;
		color: #fff;
		font-size: .65rem;
		text-align: center;
		margin: 60px 0 10px;
	}
	padding: 20px 40px;
}

@media screen and (max-width: 414px) {
	h1, h2, h3, h4, h5, h6 {
		line-height: 1.1;
	}
	.logo {
		width: 6rem;
	}
	.nav {
		padding: 4px 12px;
		line-height: 1;
	}
	.header {
		height: auto;
		background-image: none;
		.left {
			padding: 1rem 4rem;
			flex-basis: 100%;
			text-align: center;
			.img {
				display: inline-block;
			}
			h2 {
				margin-top: .5rem;
				font-weight: bold;
			}
			h4 {
				font-size: .8rem;
			}
			.desc {
				font-size: .6rem;
			}
			.nav {
				margin-top: 5px;
				font-size: .5rem;
			}
		}
		.bg {
			position: relative;
			display: block;
			.bg_img {
				display: none;
			}
		}
	}
	.page {
		text-align: center;
		flex-wrap: wrap;
		padding-left: 10px;
		padding-right: 10px;
		.left {
			padding: 10px;
		}

		.left, .right {
			flex-basis: 100%;
			margin-bottom: 20px;
		}
		.right {
			padding-right: 0;
		}
		&.page-1, &.page-2 {
			.left, .right {
				padding: 0 2rem;
			}
		}
		&.page-1 {
			.level {
				width: 100%;
				p {
					width: 100%;
					padding: 4px 12px;
				}
				.triangle {
					left: 50%;
				}
			}
		}
	}
	.extra {
		.wrap {
			width: 80%;
			margin: 0 auto;
			flex-wrap: wrap;
			.box {
				flex-basis: 100%;
			}
		}
	}
}
